.error-indicator {
  color: orange;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  font-size: large;
  text-align: center;
  margin-bottom: 1.2rem;
}

.error-indicator .image {
  margin: auto;
  width: 20%;
  height: auto;
}

.error-indicator .boom {
  font-weight: bold;
  font-size: larger;
}
